import React from 'react'
import './about.css'
import laptop from '../images/aboutImages/laptop.jpg'
function About() {

    const buttonStyle = {backgroundColor:"#97afa3", color:"#fff"}

    return (
        <>
        <strong></strong>
        <div id="about" className="about py-4 ">
                     <div className="container  mb-4 ">
                             <div className="about-title-text py-4 ">
                                 <h2 className="text-center fw-bold">About</h2>
                             </div>

                           <div className="row my-4 align-items-center align-content-center ">

                                  <div className="col-md-7 first  ">
                                      <div className="about-text  text-end text-white">
                                          <h3>Lorem ipsum dolor sit amet</h3>
                                          <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Corrupti quaerat nobis, quidem consequatur f sit amet consectetur adipisicing elit. Corrupti quaerat nobis, quidem consequatur f sit amet consectetur adipisicing elit. Corrupti quaerat nobis, quidem consequatur fugit sint.</p>
                                          <button className="btn" style={buttonStyle}>Read More..</button>
                                      </div>
                                  </div>

                                  <div className="col-md-5 second  ">
                                          <div className="about-img  text-center">
                                                <img  src={laptop} className="img-fluid laptopImg w-75 shadow-lg"/>
                                            </div>
                                  </div>
                           </div>
                     </div>
        </div>
    </>)
}

export default About
